

<template>
  <div>
    <el-table :data="tableData" style="width: 85%;margin: 20px auto;">
      <el-table-column prop="id" label="" width="100"></el-table-column>

      <el-table-column prop="m_realname" label="" width="100"></el-table-column>
      <el-table-column prop="total" label="" width="100"></el-table-column>
      <el-table-column label="">
        <el-table-column prop="one" label="" width="80"></el-table-column>
        <el-table-column prop="two" label="" width="80"></el-table-column>
        <el-table-column prop="three" label="" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="">
        <el-table-column prop="four" label="" width="80"></el-table-column>
        <el-table-column prop="five" label="" width="80"></el-table-column>
        <el-table-column prop="six" label="" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="">
        <el-table-column prop="seven" label="" width="80"></el-table-column>
        <el-table-column prop="eight" label="" width="80"></el-table-column>
        <el-table-column prop="nine" label="" width="80"></el-table-column>
      </el-table-column>
      <el-table-column label="">
        <el-table-column prop="ten" label="" width="80"></el-table-column>
        <el-table-column prop="eleven" label="" width="80"></el-table-column>
        <el-table-column prop="twelve" label="" width="80"></el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapState,mapMutations } from 'vuex'
export default {
  data() {
    return {
       organ_id:this.$route.query.id,
      tableData: [

      ]
    };
  },
  mounted(){
     this.showId(this.organ_id)
    var _this = this;
      var data=this.qs.stringify({organ_id:this.organ_id})
      this.$axios({
        url: "https://zhoutie.xiaohecheng.com/api/api/dues_list",
        header:_this.header,
        method: "POST",
        data: data
      }).then(function(res){
        console.log(res.data);
        _this.tableData = res.data.data;
        console.log(_this.tableData)
      })
  },
  methods:{
...mapMutations(['showId'])
  }
};
</script>

<style scoped>
@import url("../../static/css/cost.css");
</style>



